.btn-primary,
.btn-secondary {
    display: inline-block;
    padding: 0 3rem;
    font-family: 'Exo', sans-serif;
    font-weight: 600;
    font-size: 1.35rem;
    line-height: 4.5rem;
    border-radius: 1rem;
    cursor: pointer;
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    box-shadow: 0 0 0 0 rgba(75, 85, 230, 0.2);
}

@keyframes pulse {
    0% {
        transform: scale(0.95);
        box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.2);
        border-radius: 50px;
    }
    70% {
        transform: scale(1);
        box-shadow: 0 0 0 10px rgba(0, 0, 0, 0);
        border-radius: 50px;
    }
    100% {
        transform: scale(0.95);
        box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
        border-radius: 50px;
    }
}

.btn-primary {
    background-color: transparent;
    border-radius: 10px;
    color: #657189;
    border: 1px solid #657189;
    font-weight: 600;
    font-family: "Open Sans", sans serif;
    font-size: 1.35rem;
    &:hover {
        background-color: $primary;
        color: #fff;
        -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
        transform: scale(1.05);
        -webkit-transition: all .5s;
        -o-transition: all .5s;
        transition: all .5s;
        //animation: pulse 2s infinite;
    }
    &.white {
        border: 1px solid transparent;
        color: #000;
        background-color: white;
        &:hover {
            color: #fff;
            background-color: #000;
            border: 1px solid white;
        }
    }
}

.btn-secondary {
    border: 1px solid $primary;
    background-color: transparent;
    color: $primary;
    transition: all .3s;
    &:hover {
        border: 1px solid transparent;
        background-color: $yellow;
        color: #fff;
    }
    &.white {
        border: 1px solid white;
        color: white;
        &:hover {
            background-color: white;
            color: $yellow;
        }
    }
}

.btn-simulacao {
    background: $green;
    color: #fff;
    border: none;
}

.btn-green {
    width: 256px;
    height: 56px;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    font-weight: 600;
    color: #fff;
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}

.btn-blue {
    color: #fff;
    background-color: #002040;
}

.btn {
    display: flex;
    align-items: center;
    font-weight: 600;
    font-size: 1rem;
    color: $secondary;
    font-size: 1.2rem;
    letter-spacing: 1px;
    @include responsive (480px) {
        justify-content: center;
    }
}