.primary-title {
    font-family: 'Exo', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 35px;
    line-height: 112%;
    /* or 45px */
    letter-spacing: -1px;
    margin-bottom: 2.4rem;
    margin-top: 1.4rem;
    color: $primary;
}

.sub-titulo {
    margin-bottom: 12px;
    text-transform: uppercase;
    letter-spacing: 3px;
    color: $yellow;
    font-size: 1.2rem;
    text-transform: uppercase;
    font-weight: 600;
    font-family: 'Montserrat';
}